@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import "remixicon/fonts/remixicon.css";

@keyframes initAnima {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

:root {
  --background: #fff0f0;
  --sub-background: #f2e5e5;
  --stroke-color: #ede1e1;

  --txt-color1: #ff7474;
  --txt-color2: #2f2f2f;
  --txt-st: #bd5b5b;
  --thumbnail-ico: #af6666;
}

[data-theme="dark"] {
  --background: #201f1f;
  --sub-background: #272626;
  --stroke-color: #302e2e;

  --txt-color1: #ff7474;
  --txt-color2: #ffffff;
  --txt-st: #bd5b5b;
  --thumbnail-ico: #af6666;
}

body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background: var(--background);

  transition: background 300ms;
}

input {
  font-weight: 700;
  font-size: 1.2rem;
  line-height: 42px;
  letter-spacing: 0.05em;
  color: var(--txt-color2);

  background: var(--sub-background);
  border: 3px solid var(--stroke-color);
  border-radius: 5px;

  padding: 0.5rem 3.5rem;
  outline: none;
  transition: border-color 300ms, background 300ms, color 300ms;
}

.switch {
  position: relative;
  background: var(--txt-color2);
  width: 3rem;
  height: 1.5rem;
  border-radius: 100px;
  cursor: pointer;
  transition: background 300ms;

  &::after {
    position: absolute;
    content: "";
    width: 15px;
    height: 15px;
    border-radius: 100px;
    top: 50%;
    transform: translateY(-50%);
    margin: 0rem 0.4rem;
    background: var(--background);
    transition: transform 300ms;
  }
}

.switch.on {
  background: var(--txt-color1);
  &::after {
    transform: translate(21px, -50%);
  }
}
